<template>
  <div class="formRegister">
    <div class="container">
      <div class="row">
        <div class="col-sm-8 offset-sm-2">
          <div>
            <h2>Inscription</h2>
            <form @submit.prevent="handleSubmit">
              <div class="form-group">
                <label for="firstName">Nom</label>
                <input id="firstName" v-model="user.firstName"
                       :class="{ 'is-invalid': submitted && $v.user.firstName.$error }" class="form-control"
                       name="firstName" type="text"/>
                <div v-if="submitted && !$v.user.firstName.required" class="invalid-feedback">Le nom est requis</div>
              </div>
              <div class="form-group">
                <label for="lastName">Prénom</label>
                <input id="lastName" v-model="user.lastName"
                       :class="{ 'is-invalid': submitted && $v.user.lastName.$error }" class="form-control"
                       name="lastName" type="text"/>
                <div v-if="submitted && !$v.user.lastName.required" class="invalid-feedback">Le prénom est requis</div>
              </div>
              <div class="form-group">
                <label for="email">Email</label>
                <input id="email" v-model="user.email" :class="{ 'is-invalid': submitted && $v.user.email.$error }"
                       class="form-control" name="email" type="email"/>
                <div v-if="submitted && $v.user.email.$error" class="invalid-feedback">
                  <span v-if="!$v.user.email.required">L'email est requis</span>
                  <span v-if="!$v.user.email.email">L'email est invalide</span>
                </div>
              </div>
              <div class="form-group">
                <label for="password">Mot de passe</label>
                <input id="password" v-model="user.password"
                       :class="{ 'is-invalid': submitted && $v.user.password.$error }" class="form-control"
                       name="password" type="password"/>
                <div v-if="submitted && $v.user.password.$error" class="invalid-feedback">
                  <span v-if="!$v.user.password.required">Le mot de passe est requis</span>
                  <span v-if="!$v.user.password.minLength">6 characters minimum</span>
                </div>
              </div>
              <div class="form-group">
                <label for="confirmPassword">Confirmation</label>
                <input id="confirmPassword" v-model="user.confirmPassword"
                       :class="{ 'is-invalid': submitted && $v.user.confirmPassword.$error }" class="form-control"
                       name="confirmPassword" type="password"/>
                <div v-if="submitted && $v.user.confirmPassword.$error" class="invalid-feedback">
                  <span v-if="!$v.user.confirmPassword.required">La confirmation du mot de passe est requis</span>
                  <span
                      v-else-if="!$v.user.confirmPassword.sameAsPassword">Les mots de passe ne correspondent pas</span>
                </div>
              </div>
              <div class="form-group">
                <button class="btn btn-dark">Register</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import {required, email, minLength, sameAs} from "vuelidate/lib/validators";
export default {
  name: "Join",
  data() {
    return {
      user: {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        confirmPassword: ""
      },
      submitted: false
    };
  },
  validations: {
    user: {
      firstName: {required},
      lastName: {required},
      email: {required, email},
      password: {required, minLength: minLength(6)},
      confirmPassword: {required, sameAsPassword: sameAs('password')}
    }
  },
  methods: {
    handleSubmit() {
      this.submitted = true;
      // stop here if form is invalid
      this.$v.$touch();
      if (this.$v.$invalid) {
        return;
      }

//if you want to send any data into server before redirection then you can do it here
        this.$router.push("/Music");
    }
  }
};
</script>
<style scoped>

.formRegister {
  background: linear-gradient(rgba(0, 0, 0, 0.25), black),
  linear-gradient(90deg, #C074B2, #8AB5E8);
  font-family: Avenir, Helvetica, Arial, sans-serif;
  color: white;
  padding: 5%;
  border-radius: 5%;
  margin: 40px auto;
}
.invalid-feedback {
  font-size: 15px;
}
h2 {
  margin-bottom: 4.5rem;
}
span {
  font-size: 15px;
}
</style>

